@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@200;400;600&display=swap);
.meal-block {
  box-shadow: 0 0 10px 2px #3333331a;
  padding: 30px;
  background: #ffffff; /* Old browsers */
  background: -moz-linear-gradient(top, #ffffff 0%, #e5e5e5 100%);
  font-family: 'Poppins', sans-serif;
  border-radius: 5px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
}
.meal-preview {
  width: 100%;
  transition: opacity 0.3s ease;
}
.meal-preview:hover {
  opacity: 0.8;
}
.meal-name {
  margin: 0px;
  font-weight: bold;
  font-size: 20px;
  text-align: center;
}
.meal-description {
  margin: 5px;
  font-size: 18px;
  text-align: center;
}
.add-to-fav-btn {
  border: 0;
  padding: 20px;
  margin-top: 10px;
  margin-bottom: 0;
  text-align: center;
  border-radius: 25px;
  font-weight: bold;
  font-size: 15px;
  font-family: 'Poppins', sans-serif;
  background-color: rgb(243, 225, 255);
  transition: box-shadow 0.3s ease;
}
.hr {
  width: 100%;
}
.add-to-fav-btn:hover {
  box-shadow: 0 0 10px 5px #3333331a;
  cursor: pointer;
}

.meal-list {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr 1fr;
  padding: 30px;
  grid-gap: 30px;
  gap: 30px;
}
.error {
  color: black;
  font-weight: bold;
  font-size: 30px;
}

header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 15px;
  background-color: rgb(243, 225, 255);
  font-family: 'Poppins', sans-serif;
}
.nav-link {
  text-decoration: none;
  color: rgb(138, 35, 35);
  font-size: 26px;
  font-weight: bold;
  font-family: 'Poppins', sans-serif;
}
.header__search {
  padding: 5px;
  border-radius: 5px;
  border: none;
  font-family: 'Poppins', sans-serif;
  width: 9vw;
}
.active {
  font-size: 30px;
}

.recipe-block {
  display: flex;
  padding: 30px;
  font-family: 'Poppins', sans-serif;
  color: rgb(0, 0, 0);
}
.preview {
  width: 25vw;
  border-radius: 40px;
}
.title {
  font-weight: bold;
  font-size: 26px;
  margin: 5px;
}
.title-block p {
  font-size: 20px;
}
.instructions-block {
  padding: 20px;
}
.ingredients-heading {
  font-weight: bold;
}
.ingredients {
  font-size: 20px;
}
.instruction {
  margin-top: 100px;
  font-size: 18px;
}
.back-btn {
  width: 100px;
  font-family: 'Poppins', sans-serif;
  font-weight: bold;
  font-size: 20px;
  border: 0;
  padding: 20px;
  text-align: center;
  border-radius: 25px;
  background-color: rgb(204, 193, 178);
  margin-left: 90%;
}
.back-btn:hover {
  cursor: pointer;
}

.wrapper {
  width: 80vw;
  margin: 0 auto;
  background-color: whitesmoke;
  padding: 20px;
  font-family: 'Poppins', sans-serif;
  min-height: 500px;
  box-shadow: 0 0 10px 2px #3333331a;
}

html,
body,
* {
  margin: 0;
  padding: 0;
}

